				<text class="cell-tit clamp">运费{{goods.freightPrice==0?'(已包邮)':''}}</text>
				<text class="cell-tip">{{goods.freightPrice}}</text>
			</view>
			<view class="yt-list-cell desc-cell">
				<text class="cell-tit clamp">备注</text>
