</template>

<script>
	import api from '@/common/vmeitime-http/'
	export default {
		data() {
			return {
				maskState: false, //优惠券面板显示状态
				desc: '', //备注
				payType: 1, //1微信 2支付宝
				checkedGoodsList: [],
				goods: {},
				couponList: [],
				addressId:0,
				checkedAddress: {name:'',province:'',tel:'',city:'',county:'',addressDetail:''},
				couponId: -1,
				grouponRulesId: 0,
				grouponLinkId: 0, //参与的团购，如果是发起则为0
				cartId: 0,
				message: '',
				addressData: {
					name: '许小星',
					mobile: '13853989563',
					addressName: '金九大道',
					address: '山东省济南市历城区',
					area: '149号',
					default: false,
				}
			}
		},
		onLoad(){
			this.cartId = uni.getStorageSync('cartId')?uni.getStorageSync('cartId'):this.cartId
			this.addressId = uni.getStorageSync('addressId')?uni.getStorageSync('addressId'):this.addressId
			this.couponId = uni.getStorageSync('couponId')?uni.getStorageSync('couponId'):this.couponId
			this.grouponRulesId = uni.getStorageSync('grouponRulesId')?uni.getStorageSync('grouponRulesId'):this.grouponRulesId
			this.cartdata()
			this.compondata()
		},
		onPullDownRefresh() {
			this.cartdata(true)
			this.compondata()
		},
		methods: {
			openmask() {
				this.maskState = true
			},
			//详情页
			navToDetailPage(id) {
				uni.navigateTo({
					url: `/pages/product/product?id=${id}`
				})
			},
			//地址
			goto(id) {
				uni.navigateTo({
					url: `/pages/address/address`
				})
			},
			cartdata (flag) {
				let buyurl = `/cart/checkout?cartId=${this.cartId}&addressId=${this.addressId}&couponId=${this.couponId}&grouponRulesId=${this.grouponRulesId}`
				api.httpget(buyurl).then((res) => {
					let respons = res.data;
					if (respons.errno == 0) {
					  this.checkedGoodsList = respons.data.checkedGoodsList
					  this.goods = respons.data
					  this.checkedAddress = respons.data.checkedAddress
					  // availableCouponLength: respons.data.availableCouponLength,
					  // couponPrice: respons.data.couponPrice,
					  // grouponPrice: respons.data.grouponPrice,
					  // freightPrice: respons.data.freightPrice,
					  // goodsTotalPrice: respons.data.goodsTotalPrice,
					  // orderTotalPrice: respons.data.orderTotalPrice,
					  // this.addressId = respons.data.addressId,
					  // this.couponId = respons.data.couponId,
					  // this.grouponRulesId = respons.data.grouponRulesId
					}
					if(flag) uni.stopPullDownRefresh();
					uni.hideLoading()
				}).catch((err) => {
					uni.showToast({title: res.data.errmsg, icon: 'none'})
					uni.hideLoading()
					if(flag) uni.stopPullDownRefresh();
				})
			},
			compondata() {
				let componurl = '/coupon/selectlist?cartId=0&grouponRulesId=0'
				api.httpget(componurl).then((res) => {
					if (res.data.errno == 0) {
					  this.couponList = res.data.data.list
					}
				}).catch((err) => {})
			},
			//显示优惠券面板
			toggleMask(id){
				this.maskState = false;
				this.couponId = id
				this.cartdata()
			},
			numberChange(data) {
				this.number = data.number;
			},
			changePayType(type){
				this.payType = type;
			},
			submit(){
				let submiturl = '/order/submit'
				uni.showLoading({title: '正在加载'})
				api.httppost(submiturl,{
					addressId: this.addressId,
					cartId: this.cartId,
					couponId: this.couponId,
					grouponLinkId: 0,
					grouponRulesId: this.grouponRulesId,
					message: this.message}).then((res) => {
					if (res.data.errno == 0) {
					  this.orderId = res.data.data.orderId
					  uni.setStorageSync('couponId',0)
					  // 调用后台支付接口
					  // uni.navigateTo({
					  // 	url: `/pages/money/pay?orderId=${this.orderId}`
					  // })
					} else {
						uni.showToast({title: res.data.errmsg, icon: 'none'})
					}
					uni.hideLoading()
				}).catch((res) => {})
					uni.hideLoading()
			},
			stopPrevent(){}
		}
	}
</script>

